html, body {
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
  overflow: hidden;
  font-size: 16px;
  line-height: 20px;
}

.chat-row {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 4px 16px;
  overflow: hidden;
}

.bubble {
  max-width: 75%;
  border-radius: 25px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 8px 12px;
}

.left {
  float: left;
  background: #fff;
}

.right {
  float: right;
  color: #fff;
  background-color: #00709d;
}

.message-list {
  height: 100%;
}

.scrolly {
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}

.scrollx {
  overflow-y: hidden;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}

#root {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.app {
  height: 100%;
  position: relative;
}

.quick-replies {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
}

.replies {
  padding: 8px 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.replies button {
  display: inline-block;
  margin-right: 8px;
  font-size: 14px;
  line-height: 18px;
  padding: 8px 16px;
  background-color: #ddd;
  border-radius: 25px;
  -webkit-appearance: none;
  outline: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  border: none;
}


